.a{
    height: 100vh;
    display: flex;
    align-items: center;

}
.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;

}
.a-right{
    flex:1.25;
    
}
.a-card.bg{
    position: absolute;
    top:50px;
    left: 50px;
    background-color:#5e001f;

}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;


}
.a-img{
    width: 50%;
    height: 100%;
    object-fit: fill;
    
    object-fit: contain;
}
.a-award-img{
    width: 200px;
    height: 200px;
    border-radius: 15%;
}
.a-title{
    font-weight: 400;
    

    
}
.a-sub{
margin: 20px 0px;
     
}
.a-desc{
    font-weight:100px;
    align-content: center;
    

    

}
.a-award{
    margin-top:50px ;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.a-award-text{
    width: 77%;
  
}
.a-award-title{
    font-weight: 100;
    color:#273be2;
    
    margin-bottom:10px;
    margin-left: 23px;
    

}
@media screen and (max-width :480px) {

.a{
    flex-direction: column;
    text-align: center;
    margin-top: 50px;

}
.a-left{
     width: 100%; 
}
.a-card{
    height: 30vh;

}
.a-card.bg , .a-award{
    display: none;
}

.a-right{
    padding:20px;
    
}
    
}
